<template>
  <div class="index" >
    <el-form :inline="true" :model="formInline" class="form-inline">
        <h3>股票行情查询</h3>
      <span>后端接口：http://stock.shiguangyishi.com/list/{股票代码}</span>
      <br><br>
      <el-form-item label="股票代码：">
        <el-input v-model="formInline.code" placeholder="输入股票代码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
    <h5>示例股票代码：sh603993</h5>


    <el-row v-loading="loading">
      <el-col>
        <el-card>

          <el-row :gutter="10">
            <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
              <div style="padding: 14px;">
                <span style="font-weight: bold;">股票名字：{{name}}</span>
                <div class="bottom clearfix">
                  <p class="time">股票代码：{{ stock_code }}</p>
                  <p class="time" style="color: green;font-weight: bold;">当前价格：{{ present_price }}</p>
                  <p class="time">今日开盘价：{{ today_open }}</p>
                  <p class="time">昨日收盘价：{{ yesterday }}</p>
                  <p class="time">今日最高价：{{ today_max }}</p>
                  <p class="time">今日最低价:{{ today_mini }}</p>
                  <p class="time">成交金额:{{ turnover }}</p>
                </div>
              </div>
            </el-col>
            <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
              <el-image :src="img"></el-image>
            </el-col>

          </el-row>


        </el-card>
      </el-col>
    </el-row>




  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "List",
  data() {
    return {
      loading: false,
      formInline: {
        code: ''
      },
      img: '',//k线图
      stock_code: '',//股票代码
      name: '',//股票名字
      today_open: '',//今日开盘价
      yesterday: '',//昨日收盘价
      present_price: '',//当前价格
      today_max: '',//今日最高价
      today_mini: '',//今日最低价
      turnover: ''//成交金额

    }
  },
  //页面加载初始化
  created() {

  },
  methods:{
    //提交
    onSubmit: function () {
      // console.log(this.formInline.code);
      this.loading=true;//开启加载
      //判断股票代码是否为空
      if (this.formInline.code == "" || this.formInline.code == null || this.formInline.code == undefined){
        this.$message({
          showClose: true,
          message: '请输入股票代码进行查询！',
          type: 'warning'
        });
        this.clean();//清空数据
        this.loading=false;//关闭加载
        return false;
      }
      axios.get('http://stock.shiguangyishi.com/list/'+this.formInline.code).then(
          (resp) => {
            var data1 = resp.data;
            //判断请求是否正确
            if (data1.code == 1){
              this.$message({
                showClose: true,
                message: data1.message,
                type: 'warning'
              });
              this.clean();//清空数据
              this.loading=false;//关闭加载
              return false;
            }
            this.img = data1.img;
            this.stock_code = data1.stock_code;//股票代码
            this.name = data1.name;//股票名字
            this.today_open = data1.today_open;//今日开盘价
            this.yesterday = data1.yesterday;//昨日收盘价
            this.present_price = data1.present_price;//当前价格
            this.today_max = data1.today_max;//今日最高价
            this.today_mini = data1.today_mini;//今日最低价
            this.turnover = data1.turnover;//成交金额

            this.loading=false;//关闭加载
          }
      )
    },

    //清除数据
    clean: function () {
      this.img = '';
      this.stock_code = '';//股票代码
      this.name = '';//股票名字
      this.today_open = '';//今日开盘价
      this.yesterday = '';//昨日收盘价
      this.present_price = '';//当前价格
      this.today_max = '';//今日最高价
      this.today_mini = '';//今日最低价
      this.turnover = '';//成交金额
    }

  }
}
</script>
<style>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

</style>